<script>
  import util from '../../common/js/util'
  //import NProgress from 'nprogress'
  import SubView from '../SubView'
  import * as getData from '../../api/api';


  export default {
    components: {SubView},
    data () {
      return {
        filters: {
          name: '',
          cname: ''
        },
        list: [],
        list_key: [],
        total: 1,
        pageIndex: 1,
        pageSize: 10,
        listLoading: false,
